@import "all.less";
@baseBgColor: #008BBA;
@baseBgFontColor:#EEE;

html, body {
  height:100%;
  margin: 0;
  font-family: arial, sans-serif;
  color: #333;
}

a, abbr, button, h1, h2, h3, h4, h5, h6, label, p, small, span {
    font-smoothing: antialiased;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    text-size-adjust: 100%;
}
#banner{
    height: 60px;
    padding-left: 1px;
    background: @baseBgColor;
    color:@baseBgFontColor;
    box-shadow: 0 2px 3px 0 rgba(0,0,0,0.2);
    .menubtn{
        font-size: 1.5rem;
        cursor: pointer;
        width: 60px;
        &:hover{
           background: rgba(255,255,255,0.2);
        }
    }
    .title{
        padding-left: 6px;
        font-size: 2rem;
    }
}
#function{
    background:rgba(133, 196, 185,.7);
    font-weight:bold;
    width:180px;
    box-shadow: 2px 2px 5px 0 rgba(0,0,0,.2);
}
#testContent{
    padding-left: 6;
    padding-top: 4;
    background:rgba(191,206,198,0.2);
}
#footer{
    background: @baseBgColor;
    color: @baseBgFontColor;
    box-shadow: 0 -1px 1px 0 rgba(0,0,0,.2);
    font-size: 0.9rem;  
    height:36px;
    padding-right:4px;
    .fnbtn{
        cursor: pointer;
        width: 36px;
        &:hover{
           background: rgba(255,255,255,0.2);
        }
    }
}
